.SuperButton_button__iLVpd {
    min-width: 50px;
    min-height: 20px;
    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}

.SuperButton_button__iLVpd::after{
    display: block;
    content: '';
    position: absolute;

    left: -130%;
    top: 0;
    height: 100%;
    width: 20%;

    transform: skew(45deg);

    background-color: #fff;
    opacity: 0.7;

    z-index: 14;
}

.SuperButton_button__iLVpd:hover::after{
    animation: SuperButton_blink__31Ohe 0.33s ease;
}

.SuperButton_button__iLVpd:hover::before{
    display: block;
    content: '';
    position: absolute;

    left: -100vw;
    top: 0;
    height: 100vh;
    width: 300vw;

    background-color: #fff;
    opacity: 0.2;

    z-index: 7;
}

button:focus{
