.SuperButton_button__iLVpd:active{
    background: skyblue;
}

.SuperButton_button__iLVpd:disabled{
    color:blue;
    cursor: auto;
}

.SuperButton_button__iLVpd:disabled::after{
    z-index: -1;
}

